<template>
  <div>
    <stage-switch v-model="isPlayoff"></stage-switch>
    <div v-loading="tableData.length===0">
      <div class="table-responsive">
        <el-table
          row-key="playerId"
          :data="results.slice((currentPage-1)*pageSize,currentPage*pageSize)"
          style="margin: 0 auto"
          @sort-change="sortChange"
          @filter-change="filterChange">
          <el-table-column label=" " fixed min-width="50">
            <template slot-scope="scope">
              <span>{{ pageSize * (currentPage - 1)  + 1 + scope.$index  }}</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="name"
            label="姓名"
            fixed
            min-width="85"
            sortable>
            <template slot-scope="scope">
              <router-link :to="'/player/'+scope.row.playerId+'/'+scope.row.name" class="player-name">
                {{scope.row.name}}
              </router-link>
            </template>
          </el-table-column>
          <el-table-column
            prop="role"
            label="位置"
            min-width="75"
            sortable>
            <template slot-scope="scope">
              <div style="width: 30px;height: 30px">
                <svg viewBox="0 0 64 64" width="100%" height="100%" v-if="scope.row.role==='support'">
                  <path
                    d="M51.9,23.2H40.8V12.1A4.1,4.1,0,0,0,36.7,8H27.3a4.1,4.1,0,0,0-4.1,4.1V23.2H12.1A4.1,4.1,0,0,0,8,27.3v9.4a4.1,4.1,0,0,0,4.1,4.1H23.2V51.9A4.1,4.1,0,0,0,27.3,56h9.4a4.1,4.1,0,0,0,4.1-4.1V40.8H51.9A4.1,4.1,0,0,0,56,36.7V27.3A4.1,4.1,0,0,0,51.9,23.2Z"></path>
                </svg>
                <svg viewBox="0 0 64 64" width="100%" height="100%" v-if="scope.row.role==='offense'">
                  <rect x="12" y="49.3" width="10.2" height="5.61"></rect>
                  <path
                    d="M22.2,19.1a10.2,10.2,0,0,0,0-1c-.8-6.9-5.1-9-5.1-9s-4.3,2.1-5.1,9c0,.3,0,1,0,1V45.4H22.2Z"></path>
                  <rect x="26.9" y="49.3" width="10.2" height="5.61"></rect>
                  <path
                    d="M37.1,19.1a10.2,10.2,0,0,0,0-1C36.3,11.2,32,9,32,9s-4.3,2.1-5.1,9c0,.3,0,1,0,1V45.4H37.1Z"></path>
                  <rect x="41.8" y="49.3" width="10.2" height="5.61"></rect>
                  <path d="M52,19.1s0-.8,0-1c-.8-6.9-5.1-9-5.1-9s-4.3,2.1-5.1,9c0,.3,0,1,0,1V45.4H52Z"></path>
                </svg>
                <svg viewBox="0 0 64 64" width="100%" height="100%" v-if="scope.row.role==='tank'">
                  <path
                    d="M51.4,24.1c0,3.1,0,6.2,0,9.3a4.7,4.7,0,0,1-.6,2.4A57.2,57.2,0,0,1,33.2,55.5a1.8,1.8,0,0,1-2.4,0A57.4,57.4,0,0,1,13.2,36a5.5,5.5,0,0,1-.7-2.8c0-5.8.1-11.7,0-17.5-.1-4.2,3.2-4.9,6.1-5.6A59.4,59.4,0,0,1,32.9,8C37.5,8,44.5,9.6,47,10.4s4.1,1.4,4.3,3.3.1,3.2.1,4.9,0,3.7,0,5.5Z"></path>
                </svg>
              </div>

            </template>
          </el-table-column>
          <el-table-column
            prop="team"
            label="队伍"
            min-width="85"
            :filters="teamOptions"
            :filter-method="filterTeam"
            column-key="team"
          >
            <template slot-scope="scope">
              <div style="width:40px;text-align: center">
                <img v-if="Object.keys(teams).length!==0&&clientWidth>768" :src="teams[scope.row.teamId].logo.main.svg"
                     width="40px"
                     height="40px">
                <div>{{scope.row.team}}</div>
              </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="eliminations_avg_per_10m"
            label="消灭"
            min-width="80"
            sortable>
          </el-table-column>
          <el-table-column
            prop="deaths_avg_per_10m"
            label="阵亡"
            min-width="80"
            sortable
          >
          </el-table-column>
          <el-table-column
            prop="hero_damage_avg_per_10m"
            label="伤害"
            min-width="80"
            sortable="custom">
          </el-table-column>
          <el-table-column
            prop="healing_avg_per_10m"
            label="治疗"
            min-width="80"
            sortable>
          </el-table-column>
          <el-table-column
            prop="ultimates_earned_avg_per_10m"
            label="终极技能"
            min-width="100"
            sortable>
          </el-table-column>
          <el-table-column
            prop="final_blows_avg_per_10m"
            label="最后一击"
            min-width="100"
            sortable>
          </el-table-column>
          <el-table-column
            prop="time_played_total"
            label="游戏时间"
            min-width="120"
            sortable>
            <template slot-scope="scope">
              <span>{{scope.row.time_played_total_str}}</span>
            </template>
          </el-table-column>
        </el-table>
        <div class="pagination">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="results.length"
            :page-size="pageSize"
            @current-change="changePage">
          </el-pagination>
        </div>
      </div>

    </div>

  </div>
</template>

<script>
  import {convertSec} from "@/common/util"
  import OWL from "@/common/owl";
  import StageSwitch from "@/components/StageSwitch";

  export default {
    name: "Players",
    components: {StageSwitch},
    data() {
      return {
        isPlayoff: false,
        tableData: [],
        backupData: [],
        currentPage: 1,
        pageSize: 20,
        totalData: {2018: {}, 2019: {}}
      }
    },

    mounted() {
      const query = this.$route.query.player;
      if (query)
        this.$store.dispatch('updateSearch', query);
      this.fetchPlayers();
    },
    methods: {
      fetchPlayers() {
        this.tableData = [];
        if (this.$store.getters.getPlayerBriefs(this.season, this.isPlayoff) === undefined)
          this.$store.dispatch('setPlayersBrief', [this.season, this.isPlayoff]).then(() => {
            this.tableData = this.$store.getters.getPlayerBriefs(this.season, this.isPlayoff);
            this.backupData = this.$store.getters.getPlayerBriefs(this.season, this.isPlayoff).slice();
          })
        else {
          this.tableData = this.$store.getters.getPlayerBriefs(this.season, this.isPlayoff);
          this.backupData = this.$store.getters.getPlayerBriefs(this.season, this.isPlayoff).slice();
        }
      },
      searchPlayer(fromArr, search) {
        return fromArr.filter(player => player.name.toLowerCase().includes(search.toLowerCase()));
      },
      filterTeam() {
        return true
      },
      filterChange(f) {
        const a = [];
        if (f.team.length > 0) {
          this.backupData.forEach(player => {
            if (f.team.includes(player.team))
              a.push(player)
          });
          this.tableData = a;
        } else
          this.tableData = this.backupData.slice()

      },
      changePage(page) {
        this.currentPage = page
      },
      sortChange(c) {
        if (c.order === null) {
          this.tableData.sort((p1, p2) => p1['name'] > p2['name'] ? 1 : -1)
          ;
        } else if (c.order === 'ascending') {
          this.tableData.sort((p1, p2) => p1[c.prop] - p2[c.prop])
        } else if (c.order === 'descending') {
          this.tableData.sort((p1, p2) => p2[c.prop] - p1[c.prop])
        }
      },
    },
    watch: {
      seasonAndStage: function (newSeason) {
        if (this.totalData[this.season][this.isPlayoff] === undefined)
          this.fetchPlayers();
        else {
          this.tableData = this.totalData[this.season][this.isPlayoff];
          this.backupData = this.totalData[this.season][this.isPlayoff].slice();
        }
      }
    },
    computed: {
      teams() {
        return this.$store.getters.getAllTeams;
      },
      teamOptions() {
        return Object.values(this.teams).map(team => ({text: team.abbreviatedName, value: team.abbreviatedName}))
      },
      season() {
        return this.$store.getters.getSeason
      },
      seasonAndStage() {
        return [this.season, this.isPlayoff]
      },
      results() {
        const search = this.$store.getters.getCurrentSearch;
        if (search.length > 0) {
          this.currentPage = 1;
          return this.searchPlayer(this.tableData, search)
        }
        return this.tableData
      },
      clientWidth() {
        return this.$store.getters.getWindowParam.width;
      },

    }
  }
</script>

<style scoped>
  .pagination {
    margin-top: 10px;
  }

</style>



